<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="cahrtEl"
    :style="{width:params.canvasStyle.width,height:params.canvasStyle.height}"
  ></div>
</template>
<script>
  import * as echarts from "echarts";

  export default {
    name: "funnel",
    data() {
      return {
        myChart: null
      };
    },
    props: {
      params: {
        type: Object,
        default() {
          return {
            canvasStyle: {
              width: "500px",
              height: "500px"
            },
            options: {
              title: "",
              sort: "" // ascending || descending || none
            }
          };
        }
      }
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }
        if (this.params.data.length === 0) return;

        let chartData = this.params.data;

        let options = this.params.options;
        // 基于准备好的dom，初始化echarts实例
        this.myChart = echarts.init(this.$refs.cahrtEl);
        this.myChart.setOption({
          title: {
            // text: "年度信访量各级登记机构分布"
            text: options.title ? options.title : "",
            textStyle: {
              color: "#13f0ff",
              fontSize: "20%"
            },
            x: "center",
            y: "bottom"
          },
          tooltip: {
            show: options.tooltipShow,
            trigger: "item",
            backgroundColor: this.$STANDARD.tooltipBackgroundColor,
            padding: [10, 20],
            formatter: "{b} : {d}%"
          },
          series: [
            {
              name: "",
              type: "funnel",
              left: "10%",
              top: options.top ? options.top : "19%",
              right: "10%",
              bottom: options.bottom ? options.bottom : "5%",
              width: "66%",
              height: "76%",
              sort: options.sort ? options.sort : "",
              silent: true,
              gap: 0,
              label: {
                normal: {
                  show: true,
                  formatter: "{c}",
                  position: "center",
                  fontSize: "16%",
                  color: "#fff",
                  textShadowColor: "#000",
                  textShadowBlur: 5,
                  // textShadowOffsetX:5,
                  textShadowOffsetY: 5,
                },
                emphasis: {
                  color: "#fff"
                }
              },
              itemStyle: {
                normal: {
                  borderColor: "rgba(14,149,210,.2)",
                  borderWidth: 2,
                  color: "transparent"
                }
              },
              data: chartData
            },
            {
              name: "漏斗图",
              type: "funnel",
              left: "13%",
              top: options.borderTop ? options.borderTop : "20%",
              bottom: options.borderBot ? options.borderBot : "5%",
              width: "60%",
              height: "73%",
              sort: options.sort ? options.sort : "",
              gap: 8,
              label: {
                normal: {
                  show: true,
                  formatter: "{b}",
                  position: "right",
                  fontSize: "16%",
                  color: "#fff"
                },
                emphasis: {
                  // textStyle: {
                  //   fontSize: "20%"
                  // }
                }
              },
              labelLine: {
                normal: {
                  length: options.labelLineLen ? options.labelLineLen : 60,
                  lineStyle: {
                    width: 1,
                    type: "solid"
                  }
                }
              },
              itemStyle: {
                normal: {
                  borderColor: "transparent",
                  borderWidth: 0,
                  color: (params) => {
                    return {
                      type: 'linear',
                      x: 0,
                      y: 0,
                      x2: 0,
                      y2: 1,
                      colorStops: [{
                        offset: 0, color: options.colors[params.dataIndex][0] // 0% 处的颜色
                      }, {
                        offset: 1, color: options.colors[params.dataIndex][1]// 100% 处的颜色
                      }],
                      global: false // 缺省为 false
                    }
                  },
                }
              },
              data: chartData
            }
          ]
        });
        if (this.params.options.rollFlag) {
          this.chartMove();
          this.myChart.on("mouseover", params => {
            this.params.options.rollFlag = false;
            if (params.dataIndex === this.app.currentIndex) {
              // 序号相同不取消高亮
              return;
            } else {
              // 取消之前高亮的图形
              this.myChart.dispatchAction({
                type: "downplay",
                dataIndex: this.app.currentIndex
              });
            }
          });
          this.myChart.on("mouseout", params => {
            this.params.options.rollFlag = true;
          });
        }
      },
      // 控制tootile 的自动轮播
      chartMove() {
        this.app = {};
        this.app.currentIndex = -1;
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.interval = setInterval(() => {
          if (this.params.data.length === 0) return;
          let dataLen = this.params.data.length;
          if (this.params.options.rollFlag) {
            // 取消之前高亮的图形
            this.myChart.dispatchAction({
              type: "downplay",
              dataIndex: this.app.currentIndex
            });
            this.app.currentIndex = (this.app.currentIndex + 1) % dataLen;
            // 高亮当前图形
            this.myChart.dispatchAction({
              type: "highlight",
              dataIndex: this.app.currentIndex
            });
            this.myChart.dispatchAction({
              type: "showTip",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
          }
        }, 2000);
      }
    },
    watch: {
      'params.data': {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  };
</script>

<style lang="stylus" scoped></style>
