<template>
  <div class="wrapper child-wrapper">
    <div class="content">
      <div class="w-left">
				<ctitle :title="'区域年度受理量排行'" class="w-l-title"></ctitle>
        <div class="echarts">
          <horizontal-bar :params="xfbmBarParams"></horizontal-bar>
        </div>
      </div>
			<div class="w-right">
        <div class="c-top">
          <div class="part ct-left">
            <ctitle :title="'办理方式'"></ctitle>
            <div class="inner">
              <blfsList :data="blfsListData"></blfsList>
            </div>
          </div>
          <div class="part ct-right">
            <ctitle :title="'受理量走势'"></ctitle>
            <div class="inner">
              <timeChange class="timeChange" @change="zsTimeClick"></timeChange>
              <LineMonth :params="zsLineParams"></LineMonth>
            </div>
          </div>
        </div>
        <div class="c-bottom">
          <div class="part cb-left">
            <ctitle :title="'信访来源'"></ctitle>
            <div class="inner">
              <funnel style="margin-left:1rem;" :params="xfxsFinParams"></funnel>
            </div>
          </div>
          <div class="part cb-center">
            <ctitle :title="'投诉问题分布'"></ctitle>
            <div class="inner">
              <loopPie :params="tswtzbPieParams"></loopPie>
            </div>
          </div>
          <div class="part cb-right">
            <ctitle :title="'信访评价'"></ctitle>
            <div class="inner">
              <timeChange class="timeChange" @change="xfpjTimeClick"></timeChange>
              <LinesR :params="xfpjLineParams"></LinesR>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
	import {get_http} from "@/api/index";
	import horizontalBar from "@/components/bar/HorizontalBar";
	import ctitle from "@/components/title/index";
	import blfsList from "@/components/list/blfsList";
	import timeChange from "@/components/timeChange/index";
  import loopPie from "@/components/pie/LoopPie";
  import PieChart3D from "@/components/pie/PieChart3D";
  import BScroll from "better-scroll";
  import LinesR from "@/components/line/LinesR1";
  import funnel from "@/components/funnel/Funnel";
  import LineMonth from "@/components/line/lineMonth";
	export default {
		name: 'xfbm',
		components: {
			horizontalBar,
			ctitle,
			blfsList,
			timeChange,
			LinesR,
			loopPie,
      PieChart3D,
      funnel,
      LineMonth
		},
		data() {
			return {
        // 信访来源
        xfxsFinParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.gradientColors,
            tooltipShow: false,
            sort: "none",
            top: "10%",
            borderTop: "11%",
            rollFlag: true
          }
        },
				year: 2020,
				// 1.信访部门
        xfbmBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          }
				},
				//办理方式
				blfsListData: [],
				// 受理量走势
        zsLineParams: {
          data: [],
          colors: [
            // 紫
            {
              line: {
              //   type: 'linear',
                x: 0,
                y: 0,
                x2: 1,
                y2: 0,
                colorStops: [{
                  offset: 0,
                  color: '#6356fb'
                },
                 {
                  offset: 0.4,
                  color: '#ed9fcc'
                }, 
                {
                  offset: 1,
                  color: '#9f75e7'
                }]
              },
              start: "rgba(181, 127, 182, 0.3)",
              end: "rgba(181, 127, 182, 0)"
            },
          ],
          option: {
            splitLineShow: true,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "年",
            axisYName: "",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: false,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "5%",
            right: "15%",
            top: "15%",
            bottom: "18%",
            legendTop: "2%",
            legendRight: "15%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
				},
				//投诉问题占比
        tswtzbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: [ '#4eed99', '#d93e75', '#ccbe51', '#eb9002', '#1ba1ff', '#563ad2', '#f89e67', '#13f0ff'],
            radius: ["38%", "47%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
          }
        },
				// 信访评价
        xfpjLineParams: {
          data: [],
          colors: [
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            showSymbol: true,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisYName:"",
            axisCategoryName: "年",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "5%",
            right: "15%",
            top: "15%",
            bottom: "18%",
            legendTop: "2%",
            legendRight: "15%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {},
          xName: "name"
        }
			}
		},
		mounted() {
      
			this.getAPI(this.year);
		},
		methods: {
			// 信访量走势-时间切换
      zsTimeClick(time) {
        this.getZsLineAPI(this.year, this.curName, time);
			},
			// 信访评价-时间切换
      xfpjTimeClick(time) {
        this.getXfpjLineAPI(this.year, this.curName, time);
      },
			getAPI(year) {
        // 1区域年度信访量排行（左侧柱图）
        get_http("xfgzxspdZrdwNdsllpm", {year: year})
          .then(res => {
            this.xfbmBarParams.data = res.data.data.sort((a, b) => {return a.value - b.value;});
          })
          .catch(err => {
            console.log(err);
					});

				// 2办理方式
        get_http("xfgzxspdZrdwBlfs", {year: year})
          .then(res => {
            this.blfsListData = res.data.data;
          })
          .catch(err => {
            console.log(err);
					});

				this.getZsLineAPI()
				// 4信访来源（漏斗图）
        get_http("xfgzxspdZrdwXfxsFin", {year: year, shij_02: name})
          .then(res => {
            this.xfxsFinParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5投诉问题占比
        get_http("xfgzxspdZrdwTswtzbPie", {year: year, shij_02: name})
          .then(res => {
            this.tswtzbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
					});
					
				this.getXfpjLineAPI(year, name, '年度');
			},
			// 3受理量走势
      getZsLineAPI(year, name, time) {
        let timetype = '', _year = '';
        if (time === '年度') {
          timetype = 'cy';
          _year = '[now-4, now]'
        } else {
          timetype = 'cm';
          _year = year
        }
        get_http("xfgzxspdZrdwSllzs")
          .then(res => {
            this.zsLineParams.option.axisCategoryName = (time === '年度' ? '年' : '月');
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
			},
			// 6信访评价
      getXfpjLineAPI(year, name, time) {
        let timetype = '', _year = '';
        if (time === '年度') {
          timetype = 'cy';
          _year = '[now-4, now]'
        } else {
          timetype = 'cm';
          _year = year
        }
        get_http("xfgzxspdZrdwXfpjLine", {year: _year, shij_02: name, timetype: timetype})
          .then(res => {
            this.xfpjLineParams.option.axisCategoryName = (time === '年度' ? '年' : '月');
            this.xfpjLineParams.map = res.data.map;
            this.xfpjLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
			fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
		},

	}
</script>

<style lang="stylus" scoped>
.wrapper
	position relative
	top 0.45rem
	height calc(100% - 45px)
	.content
		width 100%
		height 100%
		display flex
		padding .2rem .3rem
		box-sizing border-box
		display flex
		.w-left
			width 3.5rem
			height 100%
			position relative
			.echarts
				height calc(100% - 0.28rem)
				box-sizing border-box
				border-right 1px #91e3f7 dashed
		.w-right
			display flex
			flex 1
			flex-direction column
			box-sizing border-box
			.part
				position relative
				flex 1
				width 100%
				height 100%
				.inner
					display flex
					height calc(100% - 0.28rem)
					justify-content center
					.chart
						width 100%
						height 100%
					.timeChange
						top 0
						right 1rem
			.c-top
				flex 1
				display flex
				position relative
				.ct-left
					flex 2
				.ct-right
					flex 1.5
			.c-bottom
				flex 1
				display flex
				.cb-left
					flex 1
				.cb-center
					flex 1
				.cb-right
					flex 1.5
</style>